@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

.container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin: 0 auto;
  }
  
  .subPagesContainer {
    display: flex;
    gap: 15px;
  }
  
  .subPagesContainer.open {
    display: block;
  }
  
  .text, .name {
    white-space: nowrap;
    margin: 0 10px;
    color: var(--primary-color);
  }
  
  .text {
    font-size: 1em;
    text-decoration: none;
  }
  
  .name {
    font-size: 22px;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
    text-decoration: none;
  }
  
  .burger {
    display: none;
    flex-direction: column;
    gap: 5px;
    cursor: pointer;
    width: 38px;
  }
  
  @media (max-width: 768px) {
    .subPagesContainer {
      display: none;
      flex-direction: column;
      position: absolute;
      top: 106px; 
      right: 45px;
      background-color: white; 
      padding: 10px 0px 10px 0px;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
      text-align: center;
    }
  
    .burger {
      display: flex; 
    }
  
    .subPagesContainer.open {
      display: flex; 
    }

    .menuItem {
        padding: 10px 0px 10px 0px;

    }
    .menuItem:hover {
        background-color: rgba(177, 146, 112, 0.198);
        font-weight: 600;
    }
  }
  @media (max-width: 480px) {
  .container {
    padding-bottom: 16px;

}}
  